import React, { Component } from 'react';
import { ThemeProvider } from 'styled-components';
import { storeProducts, detailProduct } from './data';

// Context API 
const ProductContext = React.createContext();


/*Provider = provide information for the app - passing byRef jscript*/
/** -console log callback function
    this.setState(() => {
      return {products:tempProducts, cart: [...this.state.cart,product]};
    }, () => {console.log(this.state) }); */

class ProductProvider extends Component {
  state = {
    products: [],
    details: detailProduct,
    cart: [],
    openModal: false,
    modalProduct: detailProduct,
    cartSubTotal: 0,
    cartTax: 0,
    cartTotal: 0
  };
  componentDidMount() {
    this.setProducts();
  }
  /***instead of storeProducts directly assign to products do the following */
  setProducts = () => {
    let tempProducts = [];

    storeProducts.forEach(item => {
      //assign to each item : we are copy them and not byRef
      const singleItem = { ...item };
      tempProducts = [...tempProducts, singleItem];
    });
    this.setState(() => {
      return { products: tempProducts }
    });

  };

  /**get ID */
  getItem = id => {

    const product = this.state.products.find(item => item.id === id);
    return product;
  };

  /* Methods console.log("handleDetails");  */
  handleDetail = id => {

    const product = this.getItem(id);

    this.setState(() => {
      return { details: product };
    });
  };

  addToCart = id => {
    let tempProducts = [...this.state.products];
    // console.log(`${id} in cart`);
    const index = tempProducts.indexOf(this.getItem(id));
    const product = tempProducts[index];
    product.count = product.count + 1;
    product.inCart = true;
    product.total = (product.price * product.count);
    this.openModal = false;

    this.setState(() => {
      return { products: tempProducts, cart: [...this.state.cart, product] };
    }, () => { this.addTotals() });
  }

  /** Modal */
  /**Remove from cart */


  modalOpen = id => {
    const product = this.getItem(id);
    this.modalProduct = product;

    this.setState(() => {
      return { modalProduct: product, openModal: true };
    });
  };

  modalClose = () => {
    this.setState(() => {
      return { openModal: false };
    });
  };
  /**Modal Product */


  gotoCart = () => {
    let tempprod = [...this.state.modalProduct];
    console.log(tempprod);

    /*this.setState(() => {
      return { cart : tempprod};
    });*/
  }
  increment = id => {
    //console.log('this increment');
    let tempCart = [...this.state.cart];
    const selectProduct = tempCart.find(item => item.id === id);
    const index = tempCart.indexOf(selectProduct);
    const product = tempCart[index];

    product.count = product.count + 1;
    product.total = product.count * product.price;

    this.setState(() => {
      return { cart: [...tempCart] }
    }, this.addTotals());
  }

  decrement = id => {
    //console.log('this decrement');
    let tempCart = [...this.state.cart];
    const selectProduct = tempCart.find(item => item.id === id);
    const index = tempCart.indexOf(selectProduct);
    const product = tempCart[index];

    product.count = product.count - 1;

    if (product.count === 0) {
      this.removeItem(id);
    }
    else {
      product.total = product.price * product.count;
      this.setState(() => {
        return { cart: [...tempCart] }
      }, this.addTotals());
    }
  }

  removeItem = id => {
    // console.log('this remove Item');
    let tempProduct = [...this.state.products];
    let tempCart = [...this.state.cart];
    tempCart = tempCart.filter(item => item.id !== id);
    const index = tempProduct.indexOf(this.getItem(id));
    let removeProduct = tempProduct[index];
    removeProduct.inCart = false;
    removeProduct.count = 0;
    removeProduct.total = 0;

    this.setState(() => {
      return {
        cart: [...tempCart],
        products: [...tempProduct]
      };
    }, () => {
      this.addTotals();
    });
  }

  clearCart = () => {
    //console.log('clearCart');
    this.setState(() => {
      return { cart: [] };
    }, () => {
      this.setProducts();
      this.addTotals();
    });

  }

  addTotals = id => {
    let subTotal = 0;
    console.log('add totals');
    this.state.cart.map(item => (subTotal += item.total));

    const tempTax = (parseFloat(subTotal.toFixed(2)) * parseFloat(0.1).toFixed(2));
    const tax = parseFloat(tempTax.toFixed(2));
    const total = subTotal + tax;

    this.setState(() => {
      return {
        cartSubTotal: subTotal,
        cartTax: tax,
        cartTotal: total
      }
    })
  }
  /** Testing - state by Ref change test
  tester = () => {
    console.log('State products: ', this.state.products[0].inCart);
    console.log('Data products: ', storeProducts[0].inCart);
  
    const tempProducts = [...this.state.products];
    tempProducts[0].inCart = true;
    this.setState(() => {
      return {products:tempProducts}
    },()=>{
    console.log('State products: ', this.state.products[0].inCart);
    console.log('Data products: ', storeProducts[0].inCart);
    })
  };*/

  /* Render   *tester Method<button onClick={this.tester}>test</button>*/
  render() {
    return (
      <ProductContext.Provider value={{
        ...this.state,
        handleDetail: this.handleDetail,
        addToCart: this.addToCart,
        modalOpen: this.modalOpen,
        modalClose: this.modalClose,
        gotoCart: this.gotoCart,
        increment: this.increment,
        decrement: this.decrement,
        removeItem: this.removeItem,
        clearCart: this.clearCart
      }}>
        {this.props.children}
      </ProductContext.Provider>
    );
  }
}

/* Consumer */
const ProductConsumer = ProductContext.Consumer;

export { ProductProvider, ProductConsumer };