import style from 'styled-components';

export const ButtonContainer = style.button`
text-transform:capitialize;
font-size:1rem;
background: ${props => (props.inCart ? "var(--mainYellow)" : "var(--lightBlue)")};
color:${props => (props.inCart ? "var(--lightBlue)" : "var(--mainYellow)" )};
border-radius:0.5rem;
padding:0.2rem 0.5rem;
border:0.05rem solid ${props => (props.inCart ? "var(--mainYellow)" : "var(--lightBlue)")};
border-color: ${props => (props.inCart ? "var(--mainYellow)" : "var(--lightBlue)")};


margin:0.2rem 0.5rem 0.2rem 0;
transition:all 0.5s ease-in-out;
&:hover{
  cursor: ${props => (props.inCart ? "none" : "pointer")};
  background: ${props => (props.inCart ? "var(--mainYellow)" : "var(--lightBlue)")};
  color:  ${props => (props.inCart ? "var(--lightBlue)" : "var(--mainYellow)")};
}
$:focus{
  cursor: ${props => (props.inCart ? "none" : "pointer")};
  outline:none;
}
`;


