import React, { Component } from 'react';
import style from 'styled-components';
import { ProductConsumer } from '../Context';
import { ButtonContainer } from '../components/Style/Button';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

export default class Modal extends Component {
  render() {
    return (
      <ProductConsumer>
        {(value) => {
          
          const { id,img, title, price } = value.modalProduct;
          
          if (value.openModal) {
            return (
              <ModalContainer>
                <div className="container">
                  <div className="row">
                    <div id="modal" className="col-8 mx-auto col-md-6 col-lg-4 text-center text-capitalize p-5">
                      <h5>This Item Have been added</h5>
                      <img src={img} className="img-fluid" alt="product" />
                      <h5>{title}</h5>
                      <h5 className="text-muted">price: R{price}</h5>
                      <Link to='/'>
                        <ButtonContainer 
                        onClick={() => value.modalClose()}>
                          Continue shopping
                      </ButtonContainer>
                      </Link>
                      <Link to='/cart'>
                        <ButtonContainer cart
                          onClick={() => value.modalClose()}>
                         Go to Cart
                      </ButtonContainer>  
                      </Link>
                      <Link to='/'>
                      </Link>
                    </div>
                  </div>
                </div>
              </ModalContainer>
            );
          }
          else {
             return null;
          }
        }}
      </ProductConsumer>
    );
  }
}

Modal.propTypes = {
  modalProduct: PropTypes.shape({
    id: PropTypes.number,
    img: PropTypes.string,
    title: PropTypes.string,
    price: PropTypes.number,
    inCart: PropTypes.bool
  })
};

const ModalContainer = style.div`
    position:fixed;
    height:100%;
    top:0;
    left:0;
    right:0;
    botton:0;
    background:rgba(0,0,0,0.3);
    display:flex;
    align-items:center;
    justify-content:center;
    #modal{
      background:var(--mainWhite);
    }
    `;