import React, { Component } from 'react';
import { ProductConsumer } from '../Context';
import { Link } from 'react-router-dom';
import { ButtonContainer } from './Style/Button';
import style from 'styled-components';
import PropTypes from 'prop-types';

export default class Details extends Component {
  render() {
    return (
      <ProductWrapper className="col-9 mx-auto col-md-6 col-lg-3 my-3">
      <ProductConsumer>
        {(value) => {
          const { id, company, img, info, price, title, inCart } = value.details;
          return (
            <div className="container py-5">
              <div className="row">
                <div className="col-10 mx-auto text-center text-slanter text-blue my-5">
                  <h1>{title}</h1>
                </div>
                {/**end title */}
                {/**product img */}
                <div className="row">
                  <div className="col-10 mx-auto col-md-6 my-3">
                  <h5>{title}</h5>
                  <img src={img} alt={title} className="card-img-top" />
                  </div>
                  {/**product title */}
                  <div className="col-10 mx-auto col-md-6 my-3 text-capitalize">
                    <h3>Model: {title}</h3>
                    {/**product company */}
                    <h4 className="text-title text-uppercase text-muted mt-3 mb-2">made by:
                      {company}
                    </h4>
                    <h4 className="text-title text-uppercase text-muted mt-3 mb-2">price: <span>R</span>
                      {price}
                    </h4>
                    <p className="text-capitalize font-weight-bold mt-3 mb-0">info:</p>
                    <p className="text-muted lead">{info}</p>
                    {/**Buttons */}
                    <div className="cart-btn" >
                      <Link to='/'>
                        <ButtonContainer>Back</ButtonContainer>
                      </Link>

                      <ButtonContainer disabled={inCart ? true : false}
                                onClick={() => { 
                                  value.addToCart(id);
                                  value.modalOpen(id);
                                }}
                                  className={inCart ? "in-cart" : ""}>
                        {inCart ? "in Cart" : "Add to cart"}
                      </ButtonContainer>
                    </div>
                  </div>
                </div>
              </div>

            </div>

          );

        }}
      </ProductConsumer>
    </ProductWrapper>
    )
  }
}
/**Proptypes */
Details.propTypes = {
  detailProduct: PropTypes.shape({
    id: PropTypes.number,
    img: PropTypes.string,
    title: PropTypes.string,
    price: PropTypes.number,
    inCart: PropTypes.bool
  })
};
/**Styles */
const ProductWrapper = style.div`
.in-cart{
  color:silver;
}
`;

